

.hero{
    background-image: url(/src/img/fondo.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    height: 60rem;
    position: relative;
    padding:0;

}

.contenido-hero{
    position:absolute;
    background-color: rgba(0,0,0,.6);
    background-color: rgb(0 0 0 / 70%);
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    p{
        color : $blanco;
        text-align: center;
        font-size: large;
        font-weight: 700;
    }
}


.ubicacion{
    width: 10rem;

}


.boton{    
    /// nuevo 
    background-color: $rojo;
    padding: 1.5rem 4rem;
    color: $blanco;
    margin-top: 2rem;
    font-size: 2rem;
    font-weight: 400;
    display: inline-block;
    font-weight: 700;
    border-radius: 5px;
    border: 2px solid $rojo;
    text-align: center;
    display: block;
    width: auto;
    margin: 5rem 0;
    border-radius: 2rem;

}

.boton:hover {
    cursor: pointer;
    background: $blanco;
    color: $negro;
}


.titulo{
    color: $blanco;
    font-size: 5rem;
    font-family: $fuente_principal;
    font-weight: 700;
}