.footer{
    

    p{
        color: $blanco;
        text-align: center;
    }


    .navegacion-footer{
        
        background-color: $rojo;
        width: 100%;
        height: min-content 5rem;
        font-size:large;
        font-weight: bold;
        display: flex;
        flex-direction: column;
        align-items: center;
        @include tablet{
            display: grid;
            grid-template-columns: repeat(4, 3fr);
            gap:2.5rem;
            align-items: center;
            width: auto;
        }

    }
}