.header{
    display: flex;
    flex-direction: column;
    align-items:center;
    @include tablet{
        flex-direction:row;
        justify-content: space-between;
    }
}

.titulo{
    margin: .5rem;
}


.navegacion-principal{
    display: flex;
    flex-direction: column;
    align-items: center;

    height: 0;
    visibility: hidden;
    opacity: 0;
    transition: visibility .2s ease-in-out,opacity .2s linear,height .3s linear;
    @include tablet{
        visibility: visible;
        opacity: 1;
        display: grid;
        grid-template-columns: repeat(4, 3fr);
        gap:2.5rem;
        align-items: center;
        width: auto;
        padding-right: 2rem;
    }
}

.mostrar{
    visibility: visible;
    opacity: 1;
    height: auto;
}


a{
    text-align: center;
    color: $blanco;
    font-size: 1.8rem;
}

.logo{
    height: 15rem;
    margin: 0;
    padding: 0;
    align-items: center;
}

.mobile-menu{
    @include tablet{
        display: none;
    }    
    img{
        width: 5rem;
    }
}