.contenedor{
    padding: 2rem 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 0;
    h2,h3,p{
        color: $blanco;
    }
}

.servicios{
    padding :1rem;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include telefono{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        column-gap: 5rem;
    }

    @include tablet{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: 5rem;
    }

    p{
        height : 15rem;
        padding-bottom: 2rem;
        text-align: center;
    }



    h3{
        padding: 1rem;
        margin: 0;
        font-size:2rem;
        font-weight: 700;
        height: 10rem;
        width: auto;
        text-decoration: overline #FF3028;
    }
}

.servicio{
    
    display: flex;
    flex-direction: column;
    align-items: center;

}

.iconos{
    height: 25rem;
    width: 25rem;
    background-color: #002366;
    //width: min(50rem,95%);
    border-radius: 50%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;

    img{
        height: 10rem;
        width: 10rem;
    }
}



