  display: none;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  background-color: #000000;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
  }
}

.titulo {
  margin: 0.5rem;
}

.navegacion-principal {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 0;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.2s ease-in-out, opacity 0.2s linear, height 0.3s linear;
}
@media (min-width: 768px) {
  .navegacion-principal {
    visibility: visible;
    opacity: 1;
    display: grid;
    grid-template-columns: repeat(4, 3fr);
    gap: 2.5rem;
    align-items: center;
    width: auto;
    padding-right: 2rem;
  }
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

a {
  text-align: center;
  color: #FFFFFF;
  font-size: 1.8rem;
}

.logo {
  height: 15rem;
  margin: 0;
  padding: 0;
  align-items: center;
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.footer p {
  color: #FFFFFF;
  text-align: center;
}
.footer .navegacion-footer {
  background-color: #F60F0F;
  width: 100%;
  height: min-content 5rem;
  font-size: large;
  font-weight: bold;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .footer .navegacion-footer {
    display: grid;
    grid-template-columns: repeat(4, 3fr);
    gap: 2.5rem;
    align-items: center;
    width: auto;
  }
}

.hero {
  background-image: url(/src/img/fondo.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 60rem;
  position: relative;
  padding: 0;
}

.contenido-hero {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.6);
  background-color: rgba(0, 0, 0, 0.7);
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.contenido-hero p {
  color: #FFFFFF;
  text-align: center;
  font-size: large;
  font-weight: 700;
}

.ubicacion {
  width: 10rem;
}

.boton {
  background-color: #F60F0F;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border-radius: 5px;
  border: 2px solid #F60F0F;
  text-align: center;
  display: block;
  width: auto;
  margin: 5rem 0;
  border-radius: 2rem;
}

.boton:hover {
  cursor: pointer;
  background: #FFFFFF;
  color: #000000;
}

.titulo {
  color: #FFFFFF;
  font-size: 5rem;
  font-family: "Lato", sans-serif;
  font-weight: 700;
}

.contenedor {
  padding: 2rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 0;
}
.contenedor h2, .contenedor h3, .contenedor p {
  color: #FFFFFF;
}

.servicios {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 480px) {
  .servicios {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 5rem;
  }
}
@media (min-width: 768px) {
  .servicios {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 5rem;
  }
}
.servicios p {
  height: 15rem;
  padding-bottom: 2rem;
  text-align: center;
}
.servicios h3 {
  padding: 1rem;
  margin: 0;
  font-size: 2rem;
  font-weight: 700;
  height: 10rem;
  width: auto;
  text-decoration: overline #FF3028;
}

.servicio {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.iconos {
  height: 25rem;
  width: 25rem;
  background-color: #002366;
  border-radius: 50%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.iconos img {
  height: 10rem;
  width: 10rem;
}